html {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.buy {
  box-sizing: border-box;
  background-color: #05c2df;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin: 0 37px;
  transition: all .3s;
  display: flex;
  position: relative;
  bottom: -20px;
}

.buy:hover {
  color: #05c2df;
  background-color: #fff;
}

@media (width >= 768px) {
  .buy {
    display: none;
  }
}

.buy__desktop {
  display: none;
}

@media (width >= 1280px) {
  .buy__desktop {
    width: 200px;
    height: 48px;
    margin-right: 0;
    display: flex;
    bottom: 0;
  }
}

.buy__text {
  font-size: 14px;
  font-weight: 500;
  line-height: 130%;
}

@media (width >= 1280px) {
  .buy__text {
